html {
    height: 0px;
}

nav button {
    /* position: inherit; */
    height: 60px;
    display: block;
    background: none;
    border: none;
    text-transform: uppercase;
    font-family: Montserrat, "Helvetica Neue", Helvetica, Arial, sans-serif;
    padding: 6px 12px !important;
    text-decoration: none;
    font-weight: bold;
}

nav button:hover {
    background: #203833;
    border: 1px solid #4c746c;
    border-radius: 10% / 50%;
    box-shadow: 1px 2px 5px #000000;
}

nav button:active {
    background: #203833;
    border: 1px solid #4c746c;
    border-radius: 10% / 50%;
    box-shadow: 1px 2px 5px inset #000000;
}

nav {
    background: #40685f !important;
    display: flex;
    flex-direction: row;
    position: static;
    justify-content: stretch;
    align-items: stretch;
    height: 60px;
    width: 100%;
}

#icoli {
    width: 50px;
    height: 50px;
}

body {
    background-image: url("../imgs/bosque.png");
    background-attachment: fixed;
    background-size: cover;
    background-position-x: center;
    background-position-y: top;
    background-color: #203833;
    margin: 0px;
}

.BYE {
    margin-top: 0 !important;
    background: none;
    border-radius: 10% / 50%;
    border-color: #162724;
    padding: 1vw 2vw 1vw 2vw;
    font-weight: bold !important;
    font-size: 2.5vw !important;
    color: #A5ADAB;
    margin-bottom: 15px;
}

.BYE:hover {
    animation: "luz" 250ms linear;
    animation-duration: 1000ms;
    background-color: #4c746c;
    color: #ecf1f0;
    box-shadow: 1px 2px 5px #000000;
}

.BYE:active {
    background: #203833;
    box-shadow: inset 1px 2px 5px #000000;
}

